export default new Object({
    //生成id
    randomId: function () { // 生成6位长度的字符串
        return (~~(Math.random() * (1 << 30))).toString(36); // "<<"操作相当于乘上2的n次方，"~~"相当于parseInt
    },
    /**
     * 随机数生成数据
     * number 多少位
     */
    lastrandom: '',
    random: function (number, fan) {
        let lastrandom = '';
        fan = fan || 10;
        for (let i = 0; i < number; i++) {
            lastrandom += '' + Math.floor((Math.random() * fan));
        }
        if (lastrandom != this.lastrandom) {
            this.lastrandom = lastrandom;
            return lastrandom;
        } else {
            return this.random(number, fan);
        }
    },
    /**
     * 区间随机数生成数据
     */
    intervalLastrandom: '',
    intervalRandom: function (lowerValue, upperValue) {
        let intervalLastrandom = Math.floor(Math.random() * (upperValue - lowerValue + 1) + lowerValue);
        if (intervalLastrandom != this.intervalLastrandom) {
            this.intervalLastrandom = intervalLastrandom;
            return intervalLastrandom;
        } else {
            return this.intervalRandom(lowerValue, upperValue);
        }
    },
})