<template>
  <div class="senbd">
    <div v-if="encrypt">
      <div class="top flex">
        <div class="tipinfo">{{onlinePressbinary}}</div>
        <div class="press">
          <div class="loadingeffct"></div>
        </div>
      </div>
      <div class="content">
        <div class="fls">
          <img v-if="carrierUrl" :src="carrierUrl">
          <div v-else class="explain">
            <p>1、选择一张图片进行操作</p>
            <p>2、再输入/选择一个文件进行装载</p>
            <p>3、再下载装载数据后的图片</p>
            <p class="mart">注意：装载是数据大小不能超过图片大小</p>
          </div>
        </div>
        <div class="channel">
          <p>加入</p>
        </div>
        <div class="cipher">
          <div v-if="carrierUrlOk">
            <img :src="carrierUrlOk">
          </div>
          <textarea v-if="!carrierUrlOk" v-model="carrierText" rows="4" style="resize:none"></textarea>
        </div>
      </div>
      <div class="bottom">
        <div class="flex1">
          <div @click="carrierFile" class="cy-btn">秘载体</div>
        </div>
        <div class="flex1">
          <div v-if="carrierUrlOk" @click="downloadCarrier" class="cy-btn">下载</div>
        </div>
        <div class="flex1">
          <div @click="loadData2File" class="cy-btn">装载文本</div>
          <div @click="carrierNewFile" class="cy-btn">装载文件</div>
        </div>
      </div>
    </div>
    <div v-if="solution">
      <div class="top flex">
        <div class="tipinfo">{{onlinePress2binary}}</div>
        <div class="press">
          <div class="loadingeffct"></div>
        </div>
      </div>
      <div class="content">
        <div class="fls">
          <img v-if="solutionFileUrl" :src="solutionFileUrl">
          <div v-else class="explain">
            <p>1、选择一张装载数据的图片进行操作</p>
            <p>2、再进行数据分离即可</p>
            <p class="mart">注意：选择的图片必须是本站处理的图片</p>
          </div>
        </div>
        <div class="channel">
          <p>取出</p>
        </div>
        <div class="cipher">
          <textarea v-model="solutionText" rows="4" style="resize:none"></textarea>
        </div>
      </div>
      <div class="bottom">
        <div class="flex1">
          <div @click="solutionFile" class="cy-btn">选择秘载体</div>
        </div>
        <div class="flex1">
          <div v-if="solutionFileUrlOk" class="cy-btn">
            <a :href="solutionFileUrlOk" :download="solutionText">文件下载</a>
          </div>
        </div>
        <div class="flex1">
          <div @click="solutionFile2Encrypt" class="cy-btn">数据分离</div>
        </div>
      </div>
    </div>
    <input
      v-if="tempFileInput"
      type="file"
      :id="sendInputFileId"
      @change="outputFile"
      style="display:none"
    >
    <canvas class="canvasWindow" :id="encryptCanvasID" style="display:none"></canvas>
    <canvas class="canvasWindow" :id="solutionCanvasID" style="display:none"></canvas>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import func from "../../utils/func";
import cxy_stegger from "../../utils/cxy_stegger";

export default {
  name: "PriceSendData",
  data: function() {
    return {
      tempFileInput: true,

      fileIndex: "",
      onlinePress: "", //效果
      onlinePressbinary: "", //效果
      onlinePress2: "", //效果
      onlinePress2binary: "", //效果
      sendInputFileId: "", //inputfle
      settimekey: "", //效果time
      encryptCanvasID: "", //载体canvas输出
      solutionCanvasID: "", //
      carrierText: "", //载体的密文
      solutionText: "", //解密载体的密文
      carrierUrl: "", //载体url
      carrierUrlOk: "", //载体完成url
      solutionFileUrl: "", //解密载体url
      solutionFileUrlOk: "" //解密载体完成url
    };
  },
  props: {
    encrypt: Boolean,
    solution: Boolean
  },
  computed: {
    ...mapGetters(["getNavItm"])
  },
  methods: {
    set: function(obj) {
      return new Promise(resolve => {
        for (let mkey in obj) {
          this[mkey] = obj[mkey];
        }
        this.$nextTick(function() {
          resolve();
        });
      });
    },
    //在线效果,想法:随机100以内的数字,然后转换成2进制,数字做进度
    online: function() {
      let rmky = func.randomId();
      // this.onlinePress = func.intervalRandom(0, 100);
      this.onlinePressbinary =
        rmky
          .charCodeAt(0)
          .toString(2)
          .padStart(8, 0) +
        "" +
        rmky
          .charCodeAt(1)
          .toString(2)
          .padStart(8, 0);
      // this.onlinePress2 = func.intervalRandom(0, 100);
      this.onlinePress2binary =
        rmky
          .charCodeAt(2)
          .toString(2)
          .padStart(8, 0) +
        "" +
        rmky
          .charCodeAt(3)
          .toString(2)
          .padStart(8, 0);
    },
    //文件选择后的输出
    outputFile: function(e) {
      if (e.target.files.length != 0) {
        let file = e.target.files[0];
        console.log(file);
        if (this.fileIndex == 3) {
          //被装载体
          console.log(this.fileIndex, e);
          cxy_stegger
            .encrypt(
              this.carrierUrl,
              document.getElementById(this.encryptCanvasID),
              file
            )
            .then(url => {
              this.carrierUrlOk = url;
            });
        } else if (file.type.indexOf("image") != -1) {
          //装载体
          if (this.fileIndex == 0) {
            console.log(this.fileIndex, e);
            let url = window.URL.createObjectURL(file);
            this.solutionFileUrl = url;
            // console.log(url);
          } else if (this.fileIndex == 1) {
            //载密体
            console.log(this.fileIndex, e);
            let url2 = window.URL.createObjectURL(file);
            this.carrierUrl = url2;
            // console.log(url2);
          }
        }
      }
    },
    //载体选择
    carrierFile: function() {
      this.set({
        fileIndex: 1,
        tempFileInput: false
      }).then(() => {
        this.set({
          tempFileInput: true
        }).then(() => {
          document.getElementById(this.sendInputFileId).click();
        });
      });
    },
    //被装载体选择
    carrierNewFile: function() {
      this.set({
        fileIndex: 3,
        tempFileInput: false
      }).then(() => {
        this.set({
          tempFileInput: true
        }).then(() => {
          document.getElementById(this.sendInputFileId).click();
        });
      });
    },
    //解密载体
    solutionFile: function() {
      this.set({
        fileIndex: 0,
        tempFileInput: false
      }).then(() => {
        this.set({
          tempFileInput: true
        }).then(() => {
          document.getElementById(this.sendInputFileId).click();
        });
      });
    },
    //加载数据到file中
    loadData2File: function() {
      if (!this.carrierUrl) {
        return;
      }
      if (!this.carrierText) {
        return;
      }
      cxy_stegger
        .encrypt(
          this.carrierUrl,
          document.getElementById(this.encryptCanvasID),
          this.carrierText
        )
        .then(url => {
          this.carrierUrlOk = url;
        });
    },
    //解密
    solutionFile2Encrypt: function() {
      if (this.solutionFileUrl) {
        cxy_stegger
          .solution(
            this.solutionFileUrl,
            document.getElementById(this.solutionCanvasID)
          )
          .then(res => {
            console.log(res);
            if (res.type == "text") {
              this.solutionText = res.data;
            } else {
              this.solutionText = res.filename;
              this.solutionFileUrlOk = res.url;
            }
          });
      }
    },
    //下载图片
    downloadCarrier: function() {
      cxy_stegger.downloadFile(
        "cxy_" + new Date().getTime(),
        document.getElementById(this.encryptCanvasID)
      );
      // let url = document.getElementById(this.encryptCanvasID).toDataURL("image/jpeg");
      // var oA = document.createElement("a");
      // oA.download = ''; // 设置下载的文件名，默认是'下载'
      // oA.href = url;
      // document.body.appendChild(oA);
      // oA.click();
      // oA.remove(); // 下载之后把创建的元素删除

      //如果浏览器支持msSaveOrOpenBlob方法（也就是使用IE浏览器的时候），那么调用该方法去下载图片
      // const imgUrl = this.carrierUrlOk;
      // if (window.navigator.msSaveOrOpenBlob) {
      //   var bstr = atob(imgUrl.split(",")[1]);
      //   var n = bstr.length;
      //   var u8arr = new Uint8Array(n);
      //   while (n--) {
      //     u8arr[n] = bstr.charCodeAt(n);
      //   }
      //   var blob = new Blob([u8arr]);
      //   window.navigator.msSaveOrOpenBlob(blob, "chart-download" + "." + "jpg");
      // } else {
      //   // 这里就按照chrome等新版浏览器来处理
      //   const a = document.createElement("a");
      //   a.href = imgUrl;
      //   a.setAttribute("download", "chart-download");
      //   a.click();
      // }
    }
  },
  mounted: function() {
    this.sendInputFileId = func.randomId();
    this.encryptCanvasID = func.randomId();
    this.solutionCanvasID = func.randomId();
    this.settimekey = window.setInterval(() => {
      this.online();
    }, 100);
    // console.log(cxy_stegger);
  },
  beforeDestroy: function() {
    window.clearInterval(this.settimekey);
    this.settimekey = null;
  }
};
</script>

<style scoped>
.senbd {
  border: 2px dashed #283452;
  height: 300px;
  border-top: 2px solid #00546f;
  border-bottom: 2px solid #00546f;
  border-radius: 14px;
  padding: 6px 16px;
  color: #999;
  font-weight: bold;
}

.senbd .content {
  display: flex;
  height: 251px;
}

.senbd .content .fls {
  flex: 1;
}

.senbd .content .fls img {
  margin-top: 16px;
  margin-left: 20px;
  width: 248px;
  height: 220px;
}

.senbd .content .channel {
  width: 60px;
  text-align: center;
  display: flex;
  align-items: center;
  font-size: 14px;
  font-weight: bold;
}

.senbd .content .cipher {
  flex: 1;
}

.senbd .content .cipher img {
  margin-top: 16px;
  margin-left: 20px;
  width: 248px;
  height: 220px;
}

.senbd .tipinfo {
  width: 122px;
  font-size: 12px;
}

.senbd .press {
  width: 160px;
  height: 12px;
  border: 1px solid #283452;
}

.senbd .press div {
  height: 10px;
  margin-top: 1px;
  background: #999;
}

.cy-btn {
  height: 24px;
  line-height: 24px;
  background: #999999;
  border-radius: 0px;
  border: 0px solid #00546f;
  margin-right: 16px;
}

.senbd .content textarea {
  background: #0c1326;
  resize: none;
  width: 90%;
  height: calc(220px - 22px);
  color: #999;
  font-family: auto;
  padding: 10px;
  border-color: #00546f;
  margin-top: 16px;
}

.senbd .content textarea:focus {
  outline: none;
}
.senbd .content .explain {
  font-size: 14px;
  margin: 64px 20px;
}
.senbd .bottom {
  display: flex;
}
.senbd .bottom .flex1 {
  text-align: center;
}
.loadingeffct {
  animation: loadingeffct 5s infinite;
}

@keyframes loadingeffct {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}
</style>
